:root {
  --primary-font: "Poppins", sans-serif;
}

:root {
  --sec-text-color: #79B5B8;
  --pri-text-colour: #40405E;
  --blue: #7392FD;
  --darkBlue: #0029bf;
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
  outline: 1px solid red;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--primary-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.navbar {
  position: relative;
  padding: 1.25rem;
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.navbar__logo {
  font-size: 1.5625rem;
  font-weight: 800;
  color: var(--sec-text-color);
  cursor: pointer;
}
.navbar__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.1875rem;
  background-color: var(--sec-text-color);
  transform-origin: 3px 1px;
  transition: all 0.3s ease-in-out;
}
.navbar__toggle > span:not(:last-child) {
  margin-bottom: 5px;
}
.navbar__links {
  display: flex;
}
.navbar__links li:not(:last-child) {
  margin-right: 2.3125rem;
}
.navbar__links li a {
  color: var(--black);
  transition: all 0.3s ease-in-out;
}
.navbar__links li a:hover {
  color: var(--sec-text-color);
}
.navbar__link .login {
  font-weight: 600;
  border: 1px solid var(--blue);
  border-radius: 1.0625rem;
  padding: 0.5rem 1.375rem;
  margin-right: -5px;
}
.navbar__link .login:hover {
  color: var(--white);
  background-color: var(--blue);
}
.navbar__link .signUp {
  font-weight: 600;
  border-radius: 1.0625rem;
  color: var(--white);
  padding: 0.5rem 1.375rem;
  background-color: var(--blue);
}
.navbar__link .signUp:hover {
  color: var(--white);
  background-color: #0029bf;
}

@media (min-width: 40em) {
  .p-all {
    padding-right: 3rem;
    padding-left: 3rem;
  }
}

.container {
  padding-right: 1.5625rem;
  padding-left: 1.5625rem;
}

.box-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 64em) {
  .hero_container {
    padding: 7.5rem;
  }
}
@media (min-width: 40em) {
  .hero_container {
    padding: 6.25rem;
  }
}

.hero {
  position: relative;
  text-align: center;
  white-space: normal;
  padding: 1.6875rem;
}
.hero__text {
  font-weight: 800;
  font-size: 2rem;
}
@media (min-width: 40em) {
  .hero__text span {
    color: var(--sec-text-color) !important;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25) !important;
  }
}
.hero__btn {
  position: relative;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 0.75rem 1.375rem;
  border: 1px solid var(--blue);
  border-radius: 1.25rem;
  align-items: center;
  background-color: transparent;
  color: var(--blue);
  font-size: 0.875rem;
  transition: all 0.3s ease-in-out;
}
.hero__btn img {
  width: 0.828125rem;
  transform: translateY(1.2px);
  margin-right: 0.3125rem;
}
.hero__searchBox {
  box-shadow: 20px 24px 38px 3px rgba(74, 74, 104, 0.1);
  border-radius: 0.5rem;
  padding: 0.75rem;
  width: fit-content;
  white-space: nowrap;
  text-align: center;
  display: flex;
  align-items: center;
  margin: 1.75rem 0;
  justify-content: center;
}
.hero__searchBox--item {
  cursor: pointer;
  margin-right: 0.625rem;
  display: flex;
  align-items: center;
}
.hero__searchBox--btn {
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  margin-right: 0.1875rem;
  display: inline-block;
  color: var(--white);
  padding: 0.75rem 1.5625rem;
  background-color: var(--blue);
  border-radius: 1.25rem;
}
.hero__searchBox--btn:hover {
  background-color: var(--darkBlue);
}

.img-container img {
  position: absolute;
}
.img-container img:nth-child(1) {
  left: 14.375rem;
  top: 20rem;
}
.img-container img:nth-child(2) {
  right: 12.5rem;
  top: 21.875rem;
}
.img-container img:nth-child(3) {
  left: 6.25rem;
  top: 25rem;
}
.img-container img:nth-child(4) {
  left: 6.25rem;
  top: 16.875rem;
}
.img-container img:nth-child(5) {
  right: 5rem;
  top: 13.75rem;
}
.img-container img:nth-child(6) {
  right: 3.125rem;
  top: 21.875rem;
}
.img-container img:nth-child(7) {
  right: 1.25rem;
  top: 1.875rem;
}